.sidebar {
  height: 100%;
  /* background-color: rgb(221, 195, 195) */
}
.sidebar {
  height: 100vh;
  display: grid;
  align-items: center;
  text-align: center;
  text-decoration: none;
}
.sidebar-items {
  padding: 0;
  margin: 1em;
}

.list {
  font-size: 1em;
  margin-bottom: 1.25em;
  list-style-type: none;
  text-align: center;
  text-decoration: none;
  padding: auto;
}

.list-div:hover {
  text-decoration: underline;
  transform: scale(1.25);
  background-color: rgb(126, 128, 247);
}

.list-div {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 2em;
  width: 2em;
  background-color: rgb(242, 244, 245);
  border-radius: 50%;
  transition-duration: 0.5s;
}
